import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { GridList, GridTile } from 'material-ui/GridList';

class RecentlyWathced extends Component {
  componentDidMount = () => {
    window.scrollTo(0, 0);
  }
  
  render() {
    console.log('Start render RecentlyWatched');

    let imageListContent;
    // let imageViewer;
    // let cellHeight;
    const { recentlyWatchedImgs, /*images, designType*/ } = this.props;
    
    const styles = {
      rootComponent: {
        position: 'relative',
        top: 57,
        display: 'block',
        margin: 'auto',
        color: '#550',
        // backgroundColor: '#bbf',
        width:'50%',
        float: 'right',
      },
      gridList: {
        margin: '0',
        justifyContent: 'space-around',
      },
      gridCell: {
        // flexGrow: 1,
        cursor: 'pointer',
      },
    };

    console.log(this.props.recentlyWatchedImgs);

    // let gridCols = () => {
    //     cellHeight = 180;
    //     switch (true) {
    //       case (designType === 1):
    //         return 5
    //       case (props.designType === 2):
    //         return 4
    //       case (props.designType === 3):
    //         return 3
    //       case (props.designType === 4):
    //         return 2
    //       default:
    //         return 1
    //     }
    // }

    if (recentlyWatchedImgs.length) {
      imageListContent = (
        <GridList
          // cols={gridCols()}
          cols={2}
          // cellHeight={cellHeight}
          cellHeight={270}
          style={styles.gridList}
        >
          {recentlyWatchedImgs.map((img) => {
            let imgCols, imgRows;
            if (img.imageWidth / img.imageHeight < 1) { 
              imgCols = 1; imgRows = 2;
            } else { 
              imgCols = 2; imgRows = 1;
            }
            return (
              <GridTile
                key={img.id}
                // title={img.tags}
                // subtitle={<span>by <strong>{img.user}</strong></span>}
                // actionIcon={
                //   <IconButton onClick={() => this.handleImgOpen(index)}>
                //     <ZoomIn color='white' />
                //   </IconButton>
                // }
                style={styles.gridCell}
                // onClick={() => this.handleImgOpen(index)}
                cols={imgCols}
                rows={imgRows}
              >
                <img src={img.webformatURL} alt={img.tags} className='gridIMG' />
              </GridTile>
            )
          })
          }
        </GridList>
      )
    } else {
      imageListContent = (
        <div style={styles.noMatchesWrapper} className='noMatchesText'>
          <div style={styles.noMatches}>
            unfortunately you haven't watched any images yet
          </div>
        </div>
      )
    }

    return (
      <div style={styles.rootComponent}>
        Recently Watched
        {imageListContent}
      </div>
    )
  }
}

RecentlyWathced.propTypes = {
  // fetchMoreImages: PropTypes.func,
  // fetchPage: PropTypes.func,
  // fetchViewerOpen: PropTypes.func,
  recentlyWatchedImgs: PropTypes.array,
  images: PropTypes.array,
  // totalHits: PropTypes.number,
  // page: PropTypes.number,
  designType: PropTypes.number,
  // orientation: PropTypes.string,
  // open: PropTypes.bool,
  // searchText: PropTypes.string,
}

const mapStateToProps = state => ({
  recentlyWatchedImgs: state.imgViewer.recentlyWatchedImgs,
  images: state.search.images,
  // totalHits: state.search.total,
  // page: state.search.page,
  designType: state.search.designType,
  // orientation: state.search.orientation,
  // open: state.imgViewer.open,
})

export default connect(mapStateToProps, {  })(RecentlyWathced)


